import { PopOutLine } from "@mithril-icons/clarity"
import NavLink from "Components/nav-link.js"

const isActiveRoute = (a, b) => (a == b ? "active" : "")

const SubNavbar = () => {
  const subroutes = (mdl) =>
    mdl.Routes.filter(
      (r) => r.isNav && r.group.includes(mdl.state.navState().split("/")[1])
    )
  return {
    view: ({ attrs: { mdl } }) =>
      subroutes(mdl).any() &&
      m(
        "nav.nav.navigation.container-fluid#sub-navbar",
        subroutes(mdl).map((r) =>
          r.group.includes("external")
            ? m(
                ".nav-link clear",
                m(
                  "a",
                  { target: "_blank", href: r.external },
                  r.name,
                  m(PopOutLine, {
                    margin: "8px",
                    width: "15px",
                    height: "15px",
                  })
                )
              )
            : m(NavLink, {
                mdl,
                href: r.route,
                link: r.name,
                classList: `clear ${isActiveRoute(
                  mdl.state.subnavState(),
                  r.route
                )}`,
              })
        )
      ),
  }
}

export default SubNavbar
